.button {
  font-size: 1rem;
  font-weight: bold;
  font-family: "Helvetica Neue", sans-serif;
  padding: 0.625rem 1.25rem;
  border: none;
  cursor: pointer;
}
.button--primary {
  background-color: #BD2A2E;
  color: #fff;
}
.button--primary:hover {
  background-color: rgb(147.2727272727, 32.7272727273, 35.8441558442);
}
.button--secondary {
  background-color: #3B3936;
  color: #fff;
}
.button--secondary:hover {
  background-color: rgb(32.3716814159, 31.2743362832, 29.6283185841);
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(18.75rem, 1fr));
  gap: 1.25rem;
  padding: 3.125rem;
}

.card {
  background-color: #fff;
  border: 1px solid #889C9B;
  padding: 1.25rem;
  border-radius: 0.3125rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s;
}
.card img {
  width: 100%;
  border-radius: 0.625rem;
}
.card h3 {
  font-size: 1.5rem;
  color: #BD2A2E;
  margin-top: 0.625rem;
}
.card p {
  color: #486966;
  margin: 0.625rem 0;
}
.card .card__button {
  background-color: #BD2A2E;
  color: white;
  padding: 0.625rem 1.25rem;
  border-radius: 0.3125rem;
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 0.625rem;
}
.card .card__button:hover {
  background-color: rgb(147.2727272727, 32.7272727273, 35.8441558442);
}
.card:hover {
  transform: scale(1.03);
}
.card--featured {
  border-color: #B2BEBF;
}

.header {
  background-color: #BD2A2E;
  color: #fff;
  padding: 1.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  transition: background-color 0.3s ease-in-out;
}
.header.scrolled {
  background-color: rgba(189, 42, 46, 0.8);
}
.header .header__logo .logo-img {
  width: 150px;
  height: auto;
}
.header .header__logo:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.header .header__nav {
  display: flex;
  gap: 1.25rem;
}
.header .header__nav .nav__item a {
  color: #fff;
  text-decoration: none;
  position: relative;
  padding: 0.5rem 0;
}
.header .header__nav .nav__item a.active {
  color: #B2BEBF;
}
.header .header__nav .nav__item a:hover {
  color: #B2BEBF;
}
.header .header__nav .nav__item a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -0.25rem;
  width: 100%;
  height: 2px;
  background-color: #B2BEBF;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.3s ease-in-out;
}
.header .header__nav .nav__item a:hover::after, .header .header__nav .nav__item a.active::after {
  transform: scaleX(1);
  transform-origin: left;
}

.footer {
  background: rgb(45.685840708, 44.1371681416, 41.814159292);
  color: white;
  padding: 40px 0;
  text-align: center;
}
.footer .footer__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}
@media (max-width: 768px) {
  .footer .footer__content {
    flex-direction: column;
    text-align: center;
  }
}
.footer .footer__info {
  flex: 1;
  text-align: center;
}
.footer .footer__info .footer__text {
  font-size: 0.9rem;
  margin-bottom: 10px;
  color: rgba(255, 255, 255, 0.8);
}
.footer .social-icons {
  margin-top: 10px;
}
.footer .social-icons a {
  color: white;
  margin: 0 12px;
  font-size: 1.6rem;
  transition: transform 0.3s ease, color 0.3s ease;
}
.footer .social-icons a:hover {
  color: rgb(213.5454545455, 68.4545454545, 72.4025974026);
  transform: scale(1.1);
}
.footer .social-icons a:focus {
  outline: 2px solid #BD2A2E;
  border-radius: 5px;
}
.footer .newsletter {
  flex: 1;
  text-align: center;
  margin-top: 20px;
}
.footer .newsletter h3 {
  font-size: 1.2rem;
  margin-bottom: 15px;
  color: rgba(255, 255, 255, 0.9);
  text-transform: uppercase;
  letter-spacing: 1px;
}
.footer .newsletter form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
}
.footer .newsletter input[type=email] {
  padding: 12px 15px;
  border: 2px solid transparent;
  border-radius: 25px;
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  width: 260px;
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.1);
  color: white;
}
.footer .newsletter input[type=email]::placeholder {
  color: rgba(255, 255, 255, 0.6);
}
.footer .newsletter input[type=email]:focus {
  border-color: #BD2A2E;
  box-shadow: 0 0 10px rgba(189, 42, 46, 0.5);
  background: rgba(255, 255, 255, 0.2);
}
.footer .newsletter button {
  background-color: #BD2A2E;
  color: white;
  padding: 0.625rem 1.25rem;
  border-radius: 0.3125rem;
  font-weight: bold;
  text-transform: uppercase;
  padding: 12px 20px;
  border-radius: 25px;
  font-size: 1rem;
  transition: background 0.3s ease, transform 0.2s ease-in-out;
}
.footer .newsletter button:hover {
  background-color: rgb(147.2727272727, 32.7272727273, 35.8441558442);
}
.footer .newsletter button:hover {
  background: rgb(213.5454545455, 68.4545454545, 72.4025974026);
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .footer__content {
    flex-direction: column;
    text-align: center;
  }
  .newsletter form {
    flex-direction: column;
    gap: 15px;
  }
  .social-icons {
    margin-top: 15px;
  }
}
.hero {
  background-image: url("../img/hero.png");
