  margin-bottom: 1rem;
}
.hero .hero__subtitle {
  font-size: 1.5rem;
  font-weight: normal;
  font-family: "Georgia", serif;
  margin-bottom: 2rem;
}
.hero .hero__button {
  background-color: #B2BEBF;
  color: white;
  padding: 0.625rem 1.25rem;
  border-radius: 0.3125rem;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1rem 2rem;
  font-size: 1.25rem;
}
.hero .hero__button:hover {
  background-color: rgb(150.1489361702, 166.4893617021, 167.8510638298);
}
.hero .hero__button:hover {
  background-color: rgb(150.1489361702, 166.4893617021, 167.8510638298);
}

.advertisement {
  background-color: #BD2A2E;
  color: #fff;
  padding: 4rem 1.25rem;
  text-align: center;
  position: relative;
  overflow: hidden;
}
.advertisement::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
