  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.1) 50%, rgba(255, 255, 255, 0.1) 75%, transparent 75%, transparent);
  background-size: 4rem 4rem;
  opacity: 0.5;
  z-index: 1;
}
.advertisement .advertisement__slogan {
  font-size: 1.875rem;
  font-weight: bold;
  font-family: "Georgia", serif;
  margin-bottom: 1rem;
  position: relative;
  z-index: 2;
}
.advertisement .advertisement__icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  position: relative;
  z-index: 2;
}
.advertisement .advertisement__button {
  background-color: #B2BEBF;
  color: white;
  padding: 0.625rem 1.25rem;
  border-radius: 0.3125rem;
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 1rem;
  position: relative;
  z-index: 2;
}
.advertisement .advertisement__button:hover {
  background-color: rgb(150.1489361702, 166.4893617021, 167.8510638298);
}
.advertisement .advertisement__button:hover {
  background-color: rgb(150.1489361702, 166.4893617021, 167.8510638298);
}

.categories {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  gap: 2rem;
  padding: 3.125rem;
  text-align: center;
  background-color: #889C9B;
}
.categories .category {
  background-color: #fff;
  padding: 2rem;
  text-align: center;
  border-radius: 0.3125rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.1);
  transition: transform 0.6s ease-in-out, background-color 0.6s ease-in-out;
  perspective: 1000px;
  height: 20rem;
  transform-style: preserve-3d;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.categories .category .category__front,
.categories .category .category__back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 0.3125rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.categories .category .category__front .category__icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  color: #BD2A2E;
}
.categories .category .category__front .category__name {
  font-size: 1.25rem;
  font-weight: bold;
  font-family: "Helvetica Neue", sans-serif;
  color: #486966;
}
.categories .category .category__back {
  transform: rotateY(180deg);
  padding: 2rem;
}
.categories .category .category__back .category__info {
  font-size: 1rem;
  font-weight: normal;
  font-family: "Helvetica Neue", sans-serif;
  color: #fff;
}
.categories .category:hover {
  transform: rotateY(180deg);
  background: linear-gradient(to right, #BD2A2E, rgb(222.8181818182, 110.1818181818, 113.2467532468));
}

.reviews {
  background-color: #889C9B;
  color: #486966;
  padding: 4rem 1.5rem;
  text-align: center;
}
.reviews .reviews__title {
  font-size: 2.25rem;
  font-weight: bold;
  font-family: "Georgia", serif;
  margin-bottom: 2.5rem;
  text-transform: uppercase;
  color: #BD2A2E;
}
.reviews .carousel {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.reviews .carousel .carousel-inner {
  display: flex;
  width: 100%;
  justify-content: center;
  overflow: hidden;
}
.reviews .carousel .carousel-inner .carousel-item {
  flex: 0 0 100%;
  max-width: 80%;
  padding: 2rem;
  background-color: #486966;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  text-align: center;
  transition: opacity 0.6s ease-in-out, transform 0.6s ease-in-out;
  color: #3B3936;
  visibility: hidden;
  opacity: 0;
  transform: translateX(50px);
  margin: 0 auto;
}
.reviews .carousel .carousel-inner .carousel-item.active {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
}
.reviews .carousel .carousel-control-prev,
.reviews .carousel .carousel-control-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  background-color: rgba(72, 105, 102, 0.8);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease-in-out;
}
.reviews .carousel .carousel-control-prev:hover,
.reviews .carousel .carousel-control-next:hover {
  background-color: rgb(40.8813559322, 59.6186440678, 57.9152542373);
  transform: scale(1.1);
}
.reviews .carousel .carousel-control-prev:focus,
.reviews .carousel .carousel-control-next:focus {
  outline: 2px solid #BD2A2E;
}
.reviews .carousel .carousel-control-prev {
  left: 5%;
}
.reviews .carousel .carousel-control-next {
  right: 5%;
}
.reviews .carousel .carousel-control-prev-icon,
.reviews .carousel .carousel-control-next-icon {
  filter: invert(100%);
  width: 20px;
  height: 20px;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@media (max-width: 768px) {
  .reviews .carousel-inner .carousel-item {
    max-width: 100%;
    padding: 1.5rem;
  }
  .carousel-control-prev {
    left: 2%;
  }
  .carousel-control-next {
    right: 2%;
  }
}
